.Footer {
    width: 100%;
    height: 553px;
    margin-top: 275px;
    background-color: #fff;
}

.FooterContent {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Footer h4 {
    color: #272042;
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 21px;
}

.Footer p {
    color: #8B8BA5;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 15px;
}

.FooterContentButton {
    display: flex;
    gap: 46px;
}


.Footer button {
    display: flex;
    align-items: center;
    padding-left: 22px;
    width: 210px;
    height: 46px;
    border-radius: 3px;
    background: #000;
    color: #FFF;
    font-size: 16.89px;
    font-weight: 800;
}

.Store {
    margin-right: 7px;
}

.Footer h5 {
    color: #75BF7A;
    text-align: center;
    font-size: 16px;
    font-weight: 400;
    margin-top: 76px;
}