.Get {
    display: flex;
    align-items: center;
    width: 1280px;
    height: 166px;
    background-color: #75BF7A;
    border-radius: 10px;
    margin-top: 86px;
    padding-left: 72px;
}

.Get p {
    color: #fff;
    font-size: 25px;
    margin-right: 122px;
}

.Get input {
    background-color: #fff;
    padding-left: 35px;
    margin-right: 11px;
    font-size: 20px;
    border: none;
    border-radius: 4px;
    width: 499px;
    height: 67px;
}

.Get button {
    width: 260px;
    height: 67px;
    border-radius: 4px;
    background: #000;
    color: #FFF;
    font-size: 22px;
}