.Heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 105px;
}


.Heading h1 {
    color: #000;
    font-size: 61px;
    font-weight: 8100;
    line-height: 66px; /* 108.537% */
}

.Heading p {
    color: #9E9DA2;
    font-size: 24px;
    font-weight: 500;
    line-height: 28px; /* 116.667% */
    margin-top: 27px;
    width: 550px;
    height: 120px;
}

.StoreButton {
    display: flex;
}

.Heading button {
    display: flex;
    align-items: center;
    padding-left: 20px;
    width: 260px;
    height: 67px;
    border-radius: 5px;
    background: #000;
    color: #FFF;
    font-size: 25px;
    font-weight: 700;
    margin-right: 30px;
}

.Store {
   margin-right: 10px;
}

.Drive {
    width: 553px;
    height: 582.813px;
}
