.Menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 90px;
}

.MenuNav {
    display: flex;
    align-items: center;
    gap: 46px;
}

.Menu p {
    color: #000;
    font-size: 17px;
    font-weight: 500;
}

.Menu button {
    font-size: 22px;
    font-weight: 600;
    background-color: #75BF7A;
    color: #FFF;
    border-radius: 6px;
    width: 182px;
    height: 50px;
    margin-left: 1px;
}